import { message } from 'antd';
import React, { FC, memo, useContext, useEffect, useRef, useState } from 'react';
import Helmet from 'react-helmet';
import { useTranslation } from 'react-i18next';

import { CancelContainer } from './styles';
import { history } from '../../../../services';
import { workspaceContext } from '../../../../state';
import IconClosePage from '../../../../ui/icons/IconClosePage';
import { logout } from '../../../../utils/logout';
import { checkSeparatedCheckoutUrl } from '../../../pricing/utils';
import {
  AuthContentContainer,
  AuthTextField,
  Form,
  PageWrapper,
  SubmitButton,
  Title,
} from '../../StyledComponents';
import { sendCode } from '../api';

const TwoFactor: FC = () => {
  const [isLoading, setIsLoading] = useState<boolean>(false);

  const workspaceCtx = useContext(workspaceContext);

  const { t: translation } = useTranslation();

  const codeFieldRef = useRef() as React.MutableRefObject<HTMLInputElement>;

  useEffect(() => {
    checkSeparatedCheckoutUrl();
    codeFieldRef.current.focus();
  }, []);

  const onFormSubmit = async (event: React.FormEvent<HTMLFormElement>): Promise<void> => {
    event.preventDefault();

    const code = codeFieldRef.current.value;

    // Validating code
    const isCodeValid = /^[0-9]{6}$/g.test(code);
    if (!isCodeValid) {
      message.error('Invalid two-factor code');

      return;
    }

    try {
      setIsLoading(true);

      await sendCode(code);
      history.replace('/');
    } catch {
      setIsLoading(false);
    }
  };

  const onClose = (): Promise<void> => logout(workspaceCtx);

  return (
    <PageWrapper>
      <AuthContentContainer>
        <Title>
          {translation('twoFA.enterCode')}
        </Title>
        <Form onSubmit={onFormSubmit}>
          <AuthTextField
            ref={codeFieldRef}
            placeholder='6-digit code'
            minLength={6}
            maxLength={6}
            required={true}
          />
          <SubmitButton type='submit' loading={isLoading}>
            {translation('base.submit')}
          </SubmitButton>
        </Form>
        <CancelContainer onClick={onClose}>
          <IconClosePage />
        </CancelContainer>
        <Helmet>
          <title>
            {translation('twoFA.enterCode')}
            {' '}
            - GoLogin
            {' '}
            {window.gologinAppVersion || ''}
          </title>
        </Helmet>
      </AuthContentContainer>
    </PageWrapper>
  );
};

export default memo(TwoFactor);
