import { API_BASE_URL, TWO_FA_TOKEN_KEY_NAME } from '../../../../common/constants/constants';
import { http } from '../../../services';
import { sendCredentialsToLocalServer } from '../../../services/http';

export const sendCode = async (code: string) => {
  const body = JSON.stringify({
    code,
  });

  const request = await http(`${API_BASE_URL}/2fa/authenticate`, {
    method: 'POST',
    body,
  });

  const { twoFaToken } = request.body;
  if (twoFaToken) {
    localStorage.setItem(TWO_FA_TOKEN_KEY_NAME, twoFaToken);
  }

  sendCredentialsToLocalServer('access', 'sendCode');

  return request.body;
};
